* {
  padding: 0;
  border: 0;
  background-color: rgba(0, 0, 0, 0);
  -ms-overflow-style: none;
  scrollbar-width: none;
}
*::-webkit-scrollbar {
  display: none;
}
.Safe-Area {
  width: 100%;
  height: 120px;
}
.Safe-Area-Bottom {
  width: 100%;
  height: 200px;
}
header {
  position: fixed;
  z-index: 1;
  width: 100%;
  top: 80px;
}
main {
  width: 100vw;
  height: auto;
  scroll-snap-type: proximity;
  display: flex;
  flex-direction: column;
  overflow: auto;
  flex: none;
  flex-wrap: nowrap;
}

footer {
  position: fixed;
  z-index: 1;
  bottom: 0;
  width: 100%;
  height: 180px;
  background-color: black;
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* border-top: 1px solid white; */
  border-top: 1px solid;
  border-image: linear-gradient(
    to right,
    #ffffff 0%,
    #000000 30%,
    #000000 70%,
    #e1ff1f 100%
  );
  border-image-slice: 1;
  margin: 1px;
}

.Footer_Logo {
  height: 144px;
  margin-left: 40px;
}

.Footer_Contact_Class {
  font-size: 18px;
  color: white;
  /* margin-right: 40px; */
  letter-spacing: 1px;
  margin-right: 16px;
}
.Footer_Contact_Name {
  font-size: 18px;
  color: white;
  margin-right: 13px;
  letter-spacing: 3px;
}
.Footer_Contact_Phone {
  font-size: 18px;
  color: white;
  letter-spacing: 2.45px;
  margin-bottom: 2px;
}
.Footer_Contact_Email {
  font-size: 18px;
  color: white;
  letter-spacing: 0.44px;
  margin-bottom: 6px;
}
.Footer_Slogun {
  font-size: 12px;
  color: white;
  letter-spacing: 3.2px;
  word-spacing: 3px;
}

video {
  scroll-snap-align: start;
  object-fit: cover;
}
.Video-Item {
  width: 100%;
  height: 100%;
}
.About_Text {
  font-size: 16;
  letter-spacing: 1px;
  word-spacing: 3.5px;
  line-height: 1.5;
  margin-bottom: 20px;
}
